	import trailerStars from "../../components/trailerStars.vue"
    export default {
        data() {
            return {
                carouselList: [],
				hotSuperheroList:[],
				hottrailerList:[],
				guessULike:[],
				animationData:{},
				animationDataArr:[{},{},{},{},{}]
            }
        },
		onUnload() {
			//页面卸载时清除动画数据
			this.animation = {}
			this.animationDataArr = [{},{},{},{},{}]
		},
		onPullDownRefresh() {
			this.refresh();
		},
        onLoad() {
			var _this = this;
			//创建一个临时动画对象
			this.animation = uni.createAnimation();
			//请求轮播图数据
			uni.request({
				url: _this.serverURL + '/index/carousel/list', 
				method:"POST",
				success: (res) => {
					console.log(res.data);
					if(res.data.status == 200){
						_this.carouselList = res.data.data;
					}
				}
			});
			//查询热门超英
			uni.request({
				url: _this.serverURL + '/index/movie/hot?type=superhero', 
				method:"POST",
				success: (res) => {
					console.log(res.data);
					if(res.data.status == 200){
						_this.hotSuperheroList = res.data.data;
					}
				}
			});
			//查询热门预告
			uni.request({
				url: _this.serverURL + '/index/movie/hot?type=trailer', 
				method:"POST",
				success: (res) => {
					console.log(res.data);
					if(res.data.status == 200){
						_this.hottrailerList = res.data.data;
					}
				}
			});
			this.refresh();
			
        },
        methods: {
			refresh(){
				
				var _this = this;
				
				uni.showLoading({
					mask:true
				})
				uni.showNavigationBarLoading()
				
				//查询猜你喜欢
				uni.request({
					url: _this.serverURL + '/index/guessULike', 
					method:"POST",
					success: (res) => {
						console.log(res.data);
						if(res.data.status == 200){
							_this.guessULike = res.data.data;
						}
					},
					complete: () => {
						uni.hideLoading()
						uni.hideNavigationBarLoading()
						uni.stopPullDownRefresh()
					}
				});
			},
			praiseMe(e){
				var index = e.currentTarget.dataset.index;
				//console.log(index)
				this.animation.translateY(-60).opacity(1).step({
					duration: 400 //持续时间
				})
				//导出动画数据到vue组件
				this.animationData = this.animation
				this.animationDataArr[index] = this.animationData.export()
				//还原动画
				setTimeout(function(){
					this.animation.translateY(0).opacity(0).step({
						duration: 0
					})
					this.animationData = this.animation
					this.animationDataArr[index] = this.animationData.export()
				}.bind(this),500)
			}
        },
		components:{
			trailerStars
		}
    }
